<template>
    <div>
        <label class="b-24-black" v-html="t('activity.title')"></label>
        <br><br>
        <div>
            <p class="r-16-gray px-4 pt-2 pb-1 text-center" v-html="t('activity.info')">
            </p>
        </div>
    </div>
    <div class="text-start px-4 pt-3">
        <p class="b-16-gray" v-html="t('activity.temporalidad')"></p>
        <select id="FilterTempo" class="form-control w-100 select-ap" v-model="selectedOption" @change="GetDatos(selectedOption)" v-html="t('activity.ultsem')">
           
        </select>
        <div class="numeralia d-flex py-3">
            <div class="num-circle">
                <div class="circle-black b-48-gray p-3">
                    <label class="m-auto">{{ countAudios }}</label>
                </div>
            </div>
            <div class="data-rec b-20-gray w-100">
                <label class="m-auto px-3 b-20-gray" v-html="t('activity.audio')"></label>
            </div>
        </div>
        <div class="numeralia d-flex py-3">
            <div class="num-circle">
                <div class="circle-black b-32-gray p-3">
                    <label class="m-auto">{{ minutos + 'm'}}</label>
                </div>
            </div>
            <div class="data-rec b-20-gray w-100">
                <label class="m-auto px-3 b-20-gray" v-html="t('activity.horas')"></label>
            </div>
        </div>
    </div>
    <!-- Grfica de barras -->
    <div class="py-3 px-4">
        <p class="b-16-gray pt-2 pb-1 text-start"  v-html="t('activity.dias')">
        
        </p>
        <div>
            <Bar style="overflow-y: visible;" :data="barData" :options="barOptions" />
        </div>
    </div>

    <div class="py-3 px-4">
        <p class="b-16-gray pt-2 pb-1 text-start"  v-html="t('activity.momentos')">
           
        </p>
        <div style="display: flex; justify-content: center;">
            <table border="1" style="border-collapse: collapse; width: 100%;">
                <tbody>
                    <!-- Generar filas desde la ms alta hacia la ms baja -->
                    <tr v-for="rowColor in maxRowsColor" :key="rowColor">
                    <td
                        v-for="col in columnsColor"
                        :key="col"
                        :style="{ backgroundColor: getColorAt(col, maxRowsColor - rowColor) }"
                        style="text-align:center;"
                    >
                        {{ getValueAt(col, maxRowsColor - rowColor) ?? '' }}
                    </td>
                    </tr>

                    <!-- Fila inferior con nombres de los das -->
                    <tr>
                    <td v-for="col in columnsColor" :key="col" style="font-weight: bold; text-align: center;">
                        {{ col }}
                    </td>
                    </tr>
                </tbody>
            </table>
            <!-- <table border="1" style="border-collapse: collapse; width: 100%;">
                <tbody>

                    <tr v-for="rowColor in maxRowsColor" :key="rowColor">
                        <td v-for="col in columnsColor" :key="col"
                            :style="{ backgroundColor: getValueAt(col, maxRowsColor - rowColor) ? obtenerColorAleatorio() : '#fff' }">
                            {{ getValueAt(col, maxRowsColor - rowColor) ?? '' }}
                        </td>
                    </tr>


                    <tr>
                        <td v-for="col in columnsColor" :key="col" style="font-weight: bold; text-align: center;">
                            {{ col }}
                        </td>
                    </tr>
                </tbody>
            </table> -->
        </div>
        <div class="d-flex justify-content-start pt-4">
            <div class="mini-square" style="background-color: #efefef;"></div>
            <div class="r-12-gray text-start ps-3" v-html="t('activity.registro')">
             
            </div>
        </div>
        <div class="d-flex justify-content-start pt-4">
            <div class="mini-square" style="background-color: #E3FCFF;"></div>
            <div class="r-12-gray text-start ps-3" v-html="t('activity.act')">
            </div>
        </div>
        <div class="d-flex justify-content-start pt-3">
            <div class="mini-square" style="background-color: #FFE4B3;"></div>
            <div class="r-12-gray text-start ps-3" v-html="t('activity.mente')">
            </div>
        </div>
        <div class="d-flex justify-content-start pt-3">
            <div class="mini-square" style="background-color: #FFC4B3;"></div>
            <div class="r-12-gray text-start ps-3" v-html="t('activity.lugares')">
            </div>
        </div>
        <div class="d-flex justify-content-start pt-3">
            <div class="mini-square" style="background-color: #FF9797;"></div>
            <div class="r-12-gray text-start ps-3" v-html="t('activity.palabras')">
            </div>
        </div>
        <div class="d-flex justify-content-start pt-3">
            <div class="mini-square" style="background-color: #fb5353;"></div>
            <div class="r-12-gray text-start ps-3" v-html="t('activity.hablar')">
            </div>
        </div>
    </div>
</template>

<script setup>

import { useI18n } from 'vue-i18n'
    import { ref, computed, onMounted } from 'vue'
    import { API_BASE_URL } from '@/config/api'

  const { t } = useI18n();
  const { locale } = useI18n();
  const countAudios = ref(0)
  const minutos = ref(0);
  const selectedOption = ref(1)
  const daysData = ref([0, 0, 0, 0, 0, 0, 0]); // Lunes  Domingo
const weekDays = computed(() => {
  return locale.value === 'es'
    ? ['Lun', 'Mar', 'Mi', 'Jue', 'Vie', 'Sb', 'Dom']
    : ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
});
import {
    Chart as ChartJS,
    BarElement,
    CategoryScale,
    LinearScale,
    Tooltip,
    Legend
} from 'chart.js'
import ChartDataLabels from 'chartjs-plugin-datalabels'
import { Bar } from 'vue-chartjs'

ChartJS.register(BarElement, CategoryScale, LinearScale, Tooltip, Legend, ChartDataLabels)

const barData = computed(() => ({
  labels: weekDays.value,
  datasets: [
    {
      label: '',
      data: daysData.value, //  ahora usa los datos dinmicos
      backgroundColor: '#AAAAAA',
      barThickness: 14,
      borderRadius: 10,
      pointStyle: 'circle',
    }
  ]
}))
const barOptions = {
    layout: {
        padding: {
            top: 10
        }
    },
    responsive: true,
    plugins: {
        legend: {
            display: true,
            labels: {
                padding: 10,
                color: '#555',
                boxHeight: 0
            },
        },
        datalabels: {
            anchor: 'end',
            align: 'start',
            offset: -30,
            backgroundColor: '#000000',
            borderRadius: 999,
            color: '#ffffff',
            font: {
                weight: '500',
                size: 10,
            },
            padding: {
                top: 8,
                bottom: 8,
                left: 9,
                right: 9,
            },
            formatter: (value) => value,
        },
    },
    scales: {
        y: {
            beginAtZero: true,
            ticks: {
                display: false,
            },
            grid: {
                display: true
            },
        },
        x: {
            grid: {
                display: false,
            }
        },
    }
}

// Lista de colores definida en CSS
// const colores = ["#efefef","#E3FCFF", "#FFE4B3", "#FFC4B3", "#FF9797", "#fb5353"];
const coloresMomentos = [
  "#efefef", // 0  Registro
  "#E3FCFF", // 1  Act
  "#FFE4B3", // 2  Mente
  "#FFC4B3", // 3  Lugares
  "#FF9797", // 4  Palabras
  "#fb5353"  // 5  Hablar
];
const diasOrden = ['Lunes', 'Martes', 'Mircoles', 'Jueves', 'Viernes', 'Sbado', 'Domingo'];

// Datos reactivos
const dataColorEs = ref({});

// Generar un ndice aleatorio
// function obtenerColorAleatorio() {
//     const indiceAleatorio = Math.floor(Math.random() * colores.length);
//     return colores[indiceAleatorio];
// }

// const dataColorEs = {
//   'Lun': ['08:20', '12:30', '18:00', '19:00', '22:00', '23:00', '23:30'],
//   'Mar': ['10:30', '14:30', '18:30'],
//   'Mi': ['12:30'],
//   'Jue': ['10:00', '12:00', '13:00'],
//   'Vie': ['06:00', '16:00', '19:00', '21:00'],
//   'Sb': ['09:00', '14:00', '19:00', '20:00', '22:00'],
//   'Dom': ['7:00', '14:00', '19:00']
// }
const dayMap = {
  'Lunes': 'Mon',
  'Martes': 'Tue',
  'Mircoles': 'Wed',
  'Jueves': 'Thu',
  'Viernes': 'Fri',
  'Sbado': 'Sat',
  'Domingo': 'Sun'
};

// const dayMap = {
//   'Lun': 'Mon',
//   'Mar': 'Tue',
//   'Mi': 'Wed',
//   'Jue': 'Thu',
//   'Vie': 'Fri',
//   'Sb': 'Sat',
//   'Dom': 'Sun'
// }

// Columnas traducidas
// const columnsColor = computed(() => {
//   return locale.value === 'es'
//     ? Object.keys(dataColorEs)
//     : Object.keys(dataColorEs).map(dia => dayMap[dia])
// })
const columnsColor = computed(() => {
  return locale.value === 'es'
    ? diasOrden // siempre muestra Lunes  Domingo
    : diasOrden.map(dia => dayMap[dia]);
});

// Datos traducidos
// const dataColor = computed(() => {
//   if (locale.value === 'es') return dataColorEs

//   const translated = {}
//   for (const [key, value] of Object.entries(dataColorEs)) {
//     translated[dayMap[key]] = value
//   }
//   return translated
// })
const dataColor = computed(() => {
  if (locale.value === 'es') return dataColorEs.value;

  const translated = {};
  for (const dia of diasOrden) {
    translated[dayMap[dia]] = dataColorEs.value[dia] || [];
  }
  return translated;
});

// const maxRowsColor = computed(() => {
//   return Math.max(...columnsColor.value.map(col => dataColor.value[col]?.length || 0))
// })
const maxRowsColor = computed(() => {
  return Math.max(...columnsColor.value.map(col => dataColor.value[col]?.length || 0), 0);
});

// const getValueAt = (col, rowIndex) => {
//   return dataColor.value[col]?.[rowIndex] ?? null
// }
const getValueAt = (col, rowIndex) => {
  const item = dataColor.value[col]?.[rowIndex];
  return item ? item.intervalo : null;
};
function getColorAt(col, rowIndex) {
  const item = dataColor.value[col]?.[rowIndex];
  if (!item) return "#fff";
  return coloresMomentos[item.momento] || "#fff";
}
onMounted(() => {
//   GetAudiosCount(1);
//   GetTimeAudios(1);
    GetDatos(selectedOption.value) // carga inicial
})


function GetDatos(x)
{   
    GetAudiosCount(x);
    GetTimeAudios(x);
    GetDays(x);
    GetMomentsAudios(x);
}

async function GetAudiosCount(x) {
  try {
    const sesion = localStorage.getItem("token")
    if (!sesion) return

    const response = await fetch(`${API_BASE_URL}/api/config/GetCountAudios`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({user: sesion, temporalidad: x  })
    })

    const data = await response.json()
    
    if (data.status == 1) {
      countAudios.value = data.data
    } else {
      countAudios.value = 0
  }

  } catch (error) {
    console.error("Error al cargar audios:", error)
  }
}

async function GetTimeAudios(x) {
  try {
    const sesion = localStorage.getItem("token")
    if (!sesion) return

    const response = await fetch(`${API_BASE_URL}/api/config/GetTimeAudios`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({user: sesion, temporalidad: x  })
    })

    const data = await response.json()
    
    if (data.status == 1) {
        minutos.value = Math.floor(data.data / 60);
    } else {
      minutos.value = 0
  }

  } catch (error) {
    console.error("Error al cargar audios:", error)
  }
}

async function GetDays(x) {
  try {
    const sesion = localStorage.getItem("token")
    if (!sesion) return

    const response = await fetch(`${API_BASE_URL}/api/config/GetDays`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({user: sesion, temporalidad: x  })
    })

    const data = await response.json()
    console.log(data.data)

    if(data.status == 1 && data.data != "")
    {
        const parsedData = JSON.parse(data.data);

      // Mapeo de das a ndices (en ingls por lo que llega del backend)
      const dayIndexMap = {
        Lunes: 0,
        Martes: 1,
        Mircoles: 2,
        Jueves: 3,
        Viernes: 4,
        Sbado: 5,
        Domingo: 6,
      };

      // Inicializa todos los das en 0
      const temp = [0, 0, 0, 0, 0, 0, 0];

      // Llena los valores reales de los das que vengan en el servicio
      parsedData.forEach(item => {
        const index = dayIndexMap[item.Dia];
        if (index !== undefined) {
          temp[index] = item.CantidadAudios;
        }
      });

      // Actualiza los datos de la grfica
      daysData.value = temp;
    } 
    else {
      daysData.value = [0, 0, 0, 0, 0, 0, 0];
    }
    


  } catch (error) {
    console.error("Error al cargar audios:", error)
  }
}


async function GetMomentsAudios(x) {
  try {
    const sesion = localStorage.getItem("token")
    if (!sesion) return

    const response = await fetch(`${API_BASE_URL}/api/config/GetMomentsAudios`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({user: sesion, temporalidad: x  })
    })

    const data = await response.json()
    
    if (data.status === 1 && data.data) {
      const parsed = JSON.parse(data.data);

      // Inicializa todos los das vacos
      const grouped = {};
      diasOrden.forEach(dia => { grouped[dia] = []; });

      // Agrupa los datos por da
      parsed.forEach(item => {
        const dia = item.Dia; // Ej. "Sbado"
        const intervalo = item.Intervalo;
        const momento = item.Momento;
        const cantidad = item.CantidadAudios;

        if (grouped[dia]) {
          //  Puedes repetir segn cantidad si quieres densidad visual
          for (let i = 0; i < cantidad; i++) {
            grouped[dia].push({ intervalo, momento });
          }
        }
      });

      dataColorEs.value = grouped;
      console.log("Momentos agrupados:", grouped);
    } else {
      //  Siempre mantiene estructura de das vacos
      const empty = {};
      diasOrden.forEach(dia => { empty[dia] = []; });
      dataColorEs.value = empty;
    }

  } catch (error) {
    console.error("Error al cargar audios:", error)
  }
}

</script>

<style scoped>
table td,
table th {
    border: 1px solid #ccc;
}

.h6 {
    height: 24px;
    width: 32px;
}

</style>